import React, { Component, Suspense } from 'react';
import DisplayUrl from '../../../PublishCommonComponents/DisplayUrl';
import { DisplayURL, UGC_SUITE_WALL_PREFIX } from '../../../../../Services/Api'
class YodeckUrl extends Component {

    render() {
        const { data } = this.props
        return <div className="card text-decoration-none">
            <div className="card-body p-lg-7">
                <div className="m-auto">
                    <div className="embed_code">
                        <h5 className="mb-5 fw-bolder">To Display The Social Wall With Yodeck</h5>
                        <h6>Step 1 : Copy The Display Wall URL</h6>
                        <DisplayUrl url={`${DisplayURL}${UGC_SUITE_WALL_PREFIX}${data && Object.keys(data).length > 0 ? data.wallId : null}`} />
                        <h6 className="mb-5">Step 2: Add The Social Wall To Yodeck In The Following Way</h6>
                        <ul className="list-unstyled list-points fs-7">
                            <li className="mb-3">Start by Logging in to your Yodeck account</li>
                            <li className="mb-3">Click on the Tagbox Display widget within your widget gallery to add it.</li>
                            <li className="mb-3">A Name is required for the widget and an optional Description for it.</li>
                            <li className="mb-3">Paste the "Display Wall URL" from your Tagbox Display account.</li>
                            <li className="mb-3">Set the Default Duration so that the widget will appear in a playlist.</li>
                            <li className="mb-3">Click "Save," and your widget is ready to use.</li>
                            <li className="mb-3">Enjoy the social wall experience!</li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    }
}

export default YodeckUrl;